<template>
  <el-container>
    <el-main class="view eq" style="overflow-y: visible !important">
      <el-row>
        <el-col :span="24">
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'班次时间段'"></HeaderTitle>
            <div class="taskDiv">
              <div class="dateColorLine" v-for="(item, index) in shiftArr" :key="index">
                <span style="margin-left: 10px">班次名称：</span>
                <el-input size="mini" style="width: 200px" v-model="item.name" placeholder="请输入内容"></el-input>
                <span style="margin-left: 10px">班次时间：</span>
                <el-time-picker style="margin-left: 10px" size="mini" v-model="item.sTime" format="HH:mm"
                  value-format="HH:mm"></el-time-picker>
                <span style="margin: 0 10px">至</span>
                <el-time-picker size="mini" v-model="item.eTime" format="HH:mm" value-format="HH:mm"></el-time-picker>
                <el-button style="margin: 0 10px" size="mini" icon="el-icon-plus" circle
                  @click="addColorTime(shiftArr)"></el-button>
                <el-button size="mini" icon="el-icon-minus" circle @click="minusColorTime(shiftArr, index)"></el-button>
              </div>
              <div style="margin-bottom: 10px; text-align: center">
                <el-button size="mini" type="primary" @click="saveShift()">保存</el-button>
                <el-button size="mini" @click="getSystemShift()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24">
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="`当班${ yieldKey }配置`"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>是否显示当班{{ yieldKey }}：</span>
                  <el-select size="mini" v-model="currentProduct" placeholder="请选择">
                    <el-option v-for="item in currentProductOption" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </div>
              </el-col>
              <div style="margin-bottom: 10px; text-align: center">
                <el-button size="mini" type="primary" @click="saveCurrentProduct()">保存</el-button>
                <el-button size="mini" @click="reseCurrentProduct()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24">
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'推送权限'">
              <el-button slot="head-search" class="head-search" size="mini" type="primary" @click="seniorFunc()" >高级设置</el-button>
            </HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>保养周期消息推送角色：</span>
                  <el-select size="mini" v-model="maintainRole" multiple placeholder="请选择">
                    <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId">
                    </el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>维修任务触发推送角色：</span>
                  <el-select class size="mini" v-model="repairRole" multiple placeholder="请选择">
                    <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId">
                    </el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>保养任务完成触发推送角色：</span>
                  <el-select size="mini" v-model="maintainFinishRole" multiple placeholder="请选择">
                    <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId">
                    </el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>维修任务完成触发推送角色：</span>
                  <el-select size="mini" v-model="repairFinishRole" multiple placeholder="请选择">
                    <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId">
                    </el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="24" v-if="seniorShow===true" >
                <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                  <div>
                    <span>维修任务创建时推送的url：</span>
                    <el-input size="mini" v-model="repairCreateUrl" placeholder="请输入内容"></el-input>
                  </div>
                </el-col>
                <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                  <div>
                    <span>维修任务完成时推送的url：</span>
                    <el-input size="mini" v-model="repairFinishUrl" placeholder="请输入内容"></el-input>
                  </div>
                </el-col>
                <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                  <div>
                    <span>维修任务待审核时推送的url：</span>
                    <el-input size="mini" v-model="repairReviewedUrl" placeholder="请输入内容"></el-input>
                  </div>
                </el-col>
                <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                  <div>
                    <span>保养任务待审核时推送的url：</span>
                    <el-input size="mini" v-model="maintainReviewedUrl" placeholder="请输入内容"></el-input>
                  </div>
                </el-col>
                <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                  <div>
                    <span>保养任务完成时推送的url：</span>
                    <el-input size="mini" v-model="maintainFinishUrl" placeholder="请输入内容"></el-input>
                  </div>
                </el-col>
              </el-col>
              <div style="margin-bottom: 10px; text-align: center">
                <el-button size="mini" type="primary" @click="saveRole()">保存</el-button>
                <el-button size="mini" @click="resetRole()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>
        <el-col :span="24" v-if="showHoliday === true">
          <holiday-set></holiday-set>
        </el-col>
        <el-col :span="24"  v-if="ANDON_STATUS_TIME_SHOW === true">
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'安灯设备状态异常记录阈值配置'"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>异常状态：</span>
                  <el-select size="mini" v-model="abnormalState" multiple placeholder="请选择">
                    <el-option v-for="item in deviceStateList" :key="item.value" :label="item.label" :value="item.value"></el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <div>
                  <span>异常阈值(分钟)：</span>
                  <el-input v-model="abnormalDuration" size="mini" placeholder="请输入"></el-input>
                </div>
              </el-col>
              <div style="margin-bottom: 10px; text-align: center">
                <el-button size="mini" type="primary" @click="saveAbnormalDuration()">保存</el-button>
                <el-button size="mini" @click="abnormalDuration = ''">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>
        <el-col :span="24" v-if="ANDON_STATUS_SEND_SHOW === true" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'设备异常时间消息推送配置'"></HeaderTitle>
            <div class="taskDiv">
              <div class="dateColorLine" v-for="(item, index) in abnormalPushArr" :key="index">
                <span style="margin-left: 10px">设备状态：</span>
                <el-select size="mini" v-model="item.relationType" placeholder="请选择">
                  <el-option v-for="item in deviceStateList" :key="item.value" :label="item.label" :value="Number(item.value)"></el-option>
                </el-select>
                <span style="margin-left: 10px">推送角色：</span>
                <el-select class size="mini" v-model="item.processConfiguration" multiple placeholder="请选择">
                  <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId.toString()"></el-option>
                </el-select>
                <span style="margin-left: 10px">状态持续时间：</span>
                <el-input style="width: 150px" v-model="item.timeFrequency" size="mini" placeholder="请输入"></el-input>
                <span style="margin-left: 10px">是否启用：</span>
                <el-radio-group v-model="item.isEnable">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
                <el-button style="margin: 0 10px" size="mini" icon="el-icon-plus" circle
                  @click="addAbnormalPush()"></el-button>
                <el-button size="mini" icon="el-icon-minus" circle @click="minusAbnormalPush(index)"></el-button>
              </div>
              <div style="margin-bottom: 10px; text-align: center">
                <el-button size="mini" type="primary" @click="saveAbnormalPush()">保存</el-button>
                <el-button size="mini" @click="getSystemShift()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'点检审核配置'"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <span style="margin-left: 10px">审核角色：</span>
                <el-select class size="mini" style="width: 600px;" v-model="checkReviewRole" multiple placeholder="请选择">
                  <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId"></el-option>
                </el-select>
                <span style="margin-left: 10px">是否启用：</span>
                <el-radio-group v-model="checkReviewRoleData.isEnable">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-col>
              <div style="margin-bottom: 10px;text-align: center;">
                <el-button size="mini" type="primary" @click="saveCheckReviewRole()">保存</el-button>
                <el-button size="mini" @click="getCheckReviewRole()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24" v-if="CHECK_IMG === true" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'点检超时推送配置'"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <span style="margin-left: 10px">推送角色：</span>
                <el-select class size="mini" style="width: 600px;" v-model="checkOverTimeRole" multiple placeholder="请选择">
                  <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId"></el-option>
                </el-select>
                <span style="margin-left: 10px">是否启用：</span>
                <el-radio-group v-model="checkOverTimeRoleData.isEnable">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-col>
              <div style="margin-bottom: 10px;text-align: center;">
                <el-button size="mini" type="primary" @click="saveCheckOverTimeRole()">保存</el-button>
                <el-button size="mini" @click="getCheckOverTimeRole()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle :titleName="'工具超时推送配置'"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <span style="margin-left: 10px">推送角色：</span>
                <el-select class size="mini" style="width: 600px;" v-model="toolOverTimeRole" multiple placeholder="请选择">
                  <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.roleName" :value="item.roleId"></el-option>
                </el-select>
                <span style="margin-left: 10px">是否启用：</span>
                <el-radio-group v-model="toolOverTimeRoleData.isEnable">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-col>
              <div style="margin-bottom: 10px;text-align: center;">
                <el-button size="mini" type="primary" @click="saveToolOverTimeRole()">保存</el-button>
                <el-button size="mini" @click="getToolOverTimeRole()">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle titleName="生产配置"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <span style="margin-left: 10px">产量字段配置：</span>
                <el-input size="mini" v-model="yieldFieldData.processConfiguration" placeholder="请输入内容"></el-input>
              </el-col>
              <div style="margin-bottom: 10px;text-align: center;">
                <el-button size="mini" type="primary" @click="saveYieldField()">保存</el-button>
                <el-button size="mini" @click="yieldFieldData.processConfiguration = '产量'">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>

        <el-col :span="24" >
          <el-col :span="24" class="taskCol">
            <HeaderTitle titleName="启用设备管理配置"></HeaderTitle>
            <div class="taskDiv roleConfig">
              <el-col :span="24" style="margin-top: 10px;margin-bottom: 10px;" >
                <span style="margin-left: 10px">是否启用：</span>
                <el-radio-group v-model="deviceManageConfig.isEnable">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-col>
              <div style="margin-bottom: 10px;text-align: center;">
                <el-button size="mini" type="primary" @click="saveDeviceManageConfig()">保存</el-button>
                <el-button size="mini" @click="deviceManageConfig.isEnable = 1">重置</el-button>
              </div>
            </div>
          </el-col>
        </el-col>
      </el-row>

    </el-main>
  </el-container>
</template>

<script>
/*
 * @Author: zhxy
 * @Date: 2021-08-12 09:05:28
 * @LastEditors: ZJM
 * @LastEditTime: 2022-05-22 14:28:53
 * @Description: file content
 */
// import { CommonService } from '@/services/common.service';
// 引入通用接口
// import { searchCondetionFunc } from '@/utils/publicFunction';
// 引入table组件
// import VXETable from '@/components/common/Table';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import { showWarningMessage, showSuccessMessage } from '@/utils/message';
import { SYSTEM_HOLIDAY, SYSTEM_CONFIG } from '@/utils/ajaxConfigure';
import {
  configurationGetConfigurationByEnum,
  configurationUpdateById,
  configurationGetConfigurationListByEnum,
  configurationInsertOrUpdateBatch
} from '@/api/system.js';
import holidaySet from '@/views/main/SystemConfig/holidaySet.vue';
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  STATUS_CODE,
  TITLE,
  MSG,
  VARIABLE
} from '@/utils/constantsFile';
// import HolidaySet from './holidaySet.vue';
// message service
// import { showWarningMessage } from '@/utils/message';
// import { deviceIdleGetIdleProductReport } from '@/api/statusEquipmentReport.js';
// 提示信息
// import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  roleGetRoleListAction
} from '@/api/role';
export default {
  name: 'EquipmentStatus',
  components: {
    // 加载标题组件
    HeaderTitle,
    holidaySet
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      maxHeight: document.body.clientHeight - 250,
      shiftArr: [], // 班次数据
      shiftId: 0,
      showHoliday: false,
      abnormalState: '',
      abnormalDuration: '',
      currentProduct: '0',
      currentProductOption: [
        { label: '是', value: '1' },
        { label: '否', value: '0' }
      ],
      deviceStateList: [
        {
          label: '运行',
          value: '1'
        },
        {
          label: '空闲',
          value: '2'
        },
        {
          label: '离线',
          value: '3'
        },
        {
          label: '告警',
          value: '4'
        }
      ],
      abnormal_configurationId: '',
      abnormalPushArr: [],
      abnormalPushInfo: {},
      currentProductData: {},
      roleOptions: [],
      maintainRole: [], // 保养周期消息推送角色
      repairRole: [], // 维修任务触发推送角色
      maintainRoleData: {},
      repairRoleData: {},
      maintainFinishRole: [], // 保养完成推送角色
      repairFinishRole: [], // 维修完成触发推送角色
      maintainFinishRoleData: {},
      repairFinishRoleData: {},
      repairCreateUrl: '',
      repairFinishUrl: '',
      repairReviewedUrl: '',
      maintainReviewedUrl: '',
      maintainFinishUrl: '',
      repairCreateUrlData: {},
      repairFinishUrlData: {},
      repairReviewedUrlData: {},
      maintainReviewedUrlData: {},
      maintainFinishUrlData: {},
      seniorShow: false,
      ANDON_STATUS_TIME_SHOW: false,
      ANDON_STATUS_SEND_SHOW: false,
      CHECK_REVIEW: false,
      CHECK_IMG: false,
      checkVeReviewOption: [
        { label: '是', value: '1' },
        { label: '否', value: '0' }
      ],
      checkReviewRoleData: {},
      checkReviewRole: [],
      checkReviewEnable: 0,

      checkOverTimeRoleData: {},
      checkOverTimeRole: [],

      toolOverTimeRoleData: {},
      toolOverTimeRole: [],
      yieldFieldData: {
        processConfiguration: '产量'
      },
      deviceManageConfig: {
        isEnable: 1
      }
    };
  },
  watch: {
  },
  computed: {
    yieldKey() {
      return this.$store.state.pageInfo.pageConfigInfo.yieldKey;
    }
  },
  methods: {
    seniorFunc() {
      this.seniorShow = !this.seniorShow;
    },
    async getSystemShift() {
      this.shiftArr = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'SHIFT_TIME'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          this.shiftId = data.rows.configurationId;
          if (data.rows.processConfiguration) {
            var jsonArr = JSON.parse(data.rows.processConfiguration);
            this.shiftOptions = [];
            this.shiftData = null;
            for (var key in jsonArr) {
              var json = {
                name: key,
                sTime: jsonArr[key].split('-')[0],
                eTime: jsonArr[key].split('-')[1]
              };
              this.shiftArr.push(json);
            }
          } else {
            var jsonx = {
              sTime: '',
              eTime: '',
              name: ''
            };
            this.shiftArr.push(jsonx);
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getCheckReviewRole() {
      this.checkReviewRole = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'CHECK_REVIEW'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          this.checkReviewRoleData = data.rows;
          if (data.rows.processConfiguration) {
            var arr = data.rows.processConfiguration.split(',');
            for (var i = 0; i < arr.length; i++) {
              this.checkReviewRole.push(Number(arr[i]));
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getCheckOverTimeRole() {
      this.checkReviewRole = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'CHECK_OVER_TIME_PUSH'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          this.checkOverTimeRoleData = data.rows;
          if (data.rows.processConfiguration) {
            var arr = data.rows.processConfiguration.split(',');
            for (var i = 0; i < arr.length; i++) {
              this.checkOverTimeRole.push(Number(arr[i]));
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getToolOverTimeRole() {
      this.toolOverTimeRole = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'DEVICE_TOOL_SEND_MESSAGE'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          this.toolOverTimeRoleData = data.rows;
          if (data.rows.processConfiguration) {
            var arr = data.rows.processConfiguration.split(',');
            for (var i = 0; i < arr.length; i++) {
              this.toolOverTimeRole.push(Number(arr[i]));
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getMaintainRole() {
      this.maintainRole = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'MAINTAIN_CYCLE_PUSH_ROLE'
        }, {});

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(data);
          this.maintainRoleData = data.rows;
          if (data.rows.processConfiguration) {
            var arr = data.rows.processConfiguration.split(',');
            for (var i = 0; i < arr.length; i++) {
              this.maintainRole.push(Number(arr[i]));
            }
          }
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      this.maintainFinishRole = [];
      try {
        const data = await configurationGetConfigurationByEnum({
          configurationTypeEnum: 'MAINTAIN_TASK_FINISH_PUSH_ROLE'
        }, {});

