        //   timeFrequency: Number(this.abnormalPushArr[0].timeFrequency)
        // }];
        const entityList = this.abnormalPushArr.map(item => ({
          configurationId: item.configurationId,
          relationType: item.relationType,
          processConfiguration: item.processConfiguration.join(','),
          timeFrequency: Number(item.timeFrequency),
          isEnable: item.isEnable,
          ...this.abnormalPushInfo
        }));
        const data = await configurationInsertOrUpdateBatch({}, entityList);
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage('保存成功');
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
