    async saveShift() {
      var errorFlag = 0;
      var jsonAll = {};
      for (var i = 0; i < this.shiftArr.length; i++) {
        if (!this.shiftArr[i].name || !this.shiftArr[i].sTime || !this.shiftArr[i].eTime) {
          errorFlag++;
        } else {
          jsonAll[this.shiftArr[i].name] = this.shiftArr[i].sTime + '-' + this.shiftArr[i].eTime;
        }
      }
      if (errorFlag > 0) {
        showWarningMessage('存在漏填数据，请补充完整！');
        return false;
      }
      try {
        const data = await configurationUpdateById({
          configurationId: this.shiftId,
          processConfiguration: JSON.stringify(jsonAll)
        }, {});
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.getSystemShift();
          showSuccessMessage(data.message);
        } else {
          showWarningMessage(data.message);
        }
